import React, {Component} from 'react';
import styled from 'styled-components';

import {PrimaryButton, Text, TextButton} from 'ui';
import {Wrapper, ScreenTitle, TextArea} from '../ui';

const WordButton = PrimaryButton.extend`
  width: auto;
  padding: 8px 12px;
  border-radius: 6px;
  margin: 10px 8px;
  height: 44px;
  
  ${p => p.disabled && `opacity: .5;`}
`;

const Buttons = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: auto;
  margin-bottom: 32px;
`;

export class Verify extends Component {
  state = {
    words: 'fossil  eight  comfort  potato  hunt  gaze   scale  civil  novel  hockey  glimpse  swarm'.split(' ').filter(item => item !== '').map(word => ({
      text: word,
      disabled: false
    })),
    // this.props.phrase.split(' ').map(word => ({text: word, active: false})),
    phrase: ''
  };

  activeWordsLenght = () => this.state.words.reduce((acc, item) => (!item.disabled ? acc + 1 : acc), 0);

  onClick = ({target}) => {
    const index = +target.dataset.index;
    const text = this.state.words[index].text;

    this.setState({
      words: this.state.words.map((item, itemIndex) => itemIndex === index ? {...item, disabled: true} : item),
      phrase: this.state.phrase + ' ' + text
    });
  };

  clear = () => {
    this.setState({
      words: this.state.words.map(item => ({...item, disabled: false})),
      phrase: ''
    });
  };

  confirm = () => {
    console.log('das')
  };

  render() {
    return (
     <Wrapper>
       <ScreenTitle>Let’s verify your backup phrase</ScreenTitle>
       <TextArea value={this.state.phrase} rows='4'/>
       {this.activeWordsLenght() === 0 ? <Confirm onClear={this.clear} onConfirm={this.confirm}/> : <ButtonsList words={this.state.words} onClick={this.onClick}/>}
     </Wrapper>
    );
  }
}

const ButtonsList = ({words, onClick}) => (
 <Buttons>
   {words.map(
    (word, index) => (
     <WordButton
      disabled={word.disabled}
      data-index={index}
      key={index}
      onClick={onClick}
     >{word.text}</WordButton>)
   )}
 </Buttons>
);

const Confirm = ({onClear, onConfirm}) => [
  <Text key='text' margin='auto 0 16px'>Is this correct?</Text>,
  <PrimaryButton onClick={onConfirm} key='confirm'>Confirm</PrimaryButton>,
  <TextButton onClick={onClear} key='clear' color='#fff' margin='30px 0'>Clear</TextButton>
];


