import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import {WelcomeScreen} from './screens/welcome-screen';
import {NoBitcoin} from './screens/no-bitcoin';
import {Watched} from './screens/watched';
import {Phrase} from './screens/phrase';
import {Verify} from './screens/verify';

const Context = React.createContext();

export class Welcome extends Component {
  state = {
    phrase: ''
  };

  setPhrase = phrase => {
    console.log(phrase);
    this.setState({phrase})
  };

  render() {
    return (
     <Context.Provider
      value={{
        phrase: this.state.phrase,
        setPhrase: this.setPhrase
      }}
     >
       <Route exact path={`${this.props.match.url}`} component={WelcomeScreen}/>
       <Route path={`${this.props.match.url}/bitcoin`} component={NoBitcoin}/>
       <Route path={`${this.props.match.url}/watched`} component={Watched}/>
       <Route path={`${this.props.match.url}/phrase`} component={withContext(Phrase)}/>
       <Route path={`${this.props.match.url}/verify`} component={withContext(Verify)}/>
     </Context.Provider>
    )
  }
}

const withContext = (Component) => {
  return function ConnectedComponent (props) {
    return (
     <Context.Consumer>
       {contextProps => <Component {...props} {...contextProps}/>}
     </Context.Consumer>
    )
  }
};


