import React, {Component} from 'react';
import Modal from 'react-modal';
import styled from 'styled-components';

import {TextButton} from 'ui';
import watched from '../assets/watched.svg';
import {Screen} from '../ui/templates';
import noCameraImg from '../assets/no-camera.svg';

const ModalBody = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
`;

const ModalHeader = styled.header`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 16px 0;
  background-color: #FFB300;
`;

const ModalTitle = styled.h3`
  padding: 0 24px;
  margin-top: 24px;
  font-size: 20px;
  color: #504A8E;
  letter-spacing: 0.27px;
  line-height: 30px;
  text-align: center;
`;

const ModalDesc = styled.p`
  padding: 0 24px;
  font-size: 16px;
  color: #767FAB;
  letter-spacing: 0.21px;
  text-align: center;
  line-height: 24px;
`;

export class Watched extends Component {
  state = {
    showModal: true,
    warning: true
  };

  onClick = () => {
    if (!this.state.warning) {
      this.setState({
        showModal: true
      })
    } else {
      this.props.history.push('phrase');
    }
  };

  render() {
    return [
      <Screen
       key='screen'
       title='Are  you being watched?'
       subTitle='Now is a perfect time to access yor surroundings. Nearby windows? Hidden cameras? Shoulder-spies?'
       img={watched}
       desc='Anyone with your backup phrase can access or spend your bitcoin.'
       button={{
         text: 'Got It',
         onClick: this.onClick
       }}
      />,
     <Modal
      key='modal'
      isOpen={this.state.showModal}
      className="modal-content watched-content"
      overlayClassName="modal-overlay"
     >
       <ModalBody>
         <ModalHeader>
           <img src={noCameraImg} alt=""/>
         </ModalHeader>
         <ModalTitle>Screenshots are not secure</ModalTitle>
         <ModalDesc>
           If you take a screenshot, your backup may be viewed by
           other apps. You can make a safe backup with physical paper and a pen.
         </ModalDesc>
         <TextButton
          color='#FFB300'
          margin='0 0 24px'
          onClick={() => this.setState({warning: true, showModal: false})}
         >I understand</TextButton>
       </ModalBody>
     </Modal>
    ]
  }
}


